import uniIcons from "@/components/uni-icons/uni-icons.vue"
import liuyunoTabs from "@/components/liuyuno-tabs/liuyuno-tabs.vue";
import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex';
export default {
	components: {
		uniIcons,
		liuyunoTabs
	},
	data() {
		return {
			currentTab: 0,
			tabList: ['网易新闻', '腾讯新闻'],
			page: 0
		}
	},
	computed: {
		...mapState('News', ['newsList'])
	},
	onLoad() {
		uni.showLoading({
			title: '正在加载中',
			mask: true
		})
		uni.request({




			url: 'https://api.apiopen.top/getWangYiNews',

			method: 'POST',
			success: (res) => {
				uni.hideLoading()
				if (res.data.code == 200) {
					this.getNewsList(res.data.result)
				} else {
					uni.showToast({
						title: res.data.message
					})
				}

			},
			fail: (res) => {
				debugger;
			}
		})
	},
	methods: {
		tabClick(e) {
			console.log('e',e)
			this.currentTab = e
			console.log('currentTab',this.currentTab)
			if (this.newsList[this.currentTab] == undefined) {
				let url = ''
				switch (e) {
					case 1:





						url:
							`https://pacaio.match.qq.com/irs/rcd?cid=56&ext=games&token=c786875b8e04da17b24ea5e332745e0f&num=20&expIds=20190106A13PFT%7C20190108A04MLS&page=${this.page}`

				}
				uni.showToast({
					title: '正在加载'
				})
				uni.request({
					url: url,
					method: 'POST',
					success: (res) => {
					console.log('res',res)
					},
				})
			}
		},
		...mapActions('News', ['getNewsList'])
	},
	filters: {
		ellipsis(text) {
			if (text.length > 24) {
				text = text.substr(0, 25)
				text += "..."
			}
			return text
		}
	}
}
